.App {
  min-height: 100vh;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
.main {
  position: absolute;
  top: 7.5rem;
  left: 50%;
  transform: translateX(-50%);
  max-width: 58rem;
  width: 90%;
  display: flex;
  flex-direction: column;
  @include respond(phone) {
    top: 6rem;
  }
}
